import React, { Component } from 'react'
import uuid from 'uuid'
import PropStypes from 'prop-types'
export default class Formulario extends Component {
    descripcionRef;
    montoRef;
    fechaRef;
    
constructor(props){
    super(props)
    this.descripcionRef = React.createRef();
    this.montoRef = React.createRef();
    this.fechaRef = React.createRef();
    this.state = {
        error:false
    }
}

    enviarGasto = (e)=>{
        e.preventDefault();

        if(this.descripcionRef.current.value=="" || this.montoRef.current.value=="" || this.fechaRef ==""){
            this.setState({
                error:true
            })
            return
        }else{
            
        let objGasto = {
            id:uuid(),
            descripcion:this.descripcionRef.current.value,
            monto:this.montoRef.current.value,
            fecha:this.fechaRef.current.value,
        }
        e.currentTarget.reset()
        
        this.props.agregarGasto(objGasto);
        this.setState({
            error:false
        })
        }
        
        
    }

    render() {
        return (
            <form onSubmit={this.enviarGasto}>
            <fieldset>
                <legend>Gastos y presupuestos</legend>
                <div className="form row">
                <label htmlFor="staticEmail" className="col-sm-2 col-form-label">Email</label>
                <div className="col-sm-10">
                    <input type="text" readOnly="" className="form-control-plaintext" id="staticEmail" defaultValue="email@example.com"/>
                </div>
                </div>
                <div className="form-group">
                <label htmlFor="exampleInputEmail1">Descripcion del gasto</label>
                <input  ref={this.descripcionRef} type="text" className="form-control" id="exampleInputEmail1" placeholder="Ejemplo: Comida"/>
                <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                </div>
                <div className="form-group">
                <label htmlFor="exampleInputPassword1">Monto del gasto</label>
                <input  ref={this.montoRef} type="number" className="form-control" id="exampleInputPassword1" placeholder="Ejemplo 100.00"/>
                </div>
                <div className="form-group">
                <label htmlFor="fechagasto">Fecha del gasto</label>
                <input  ref={this.fechaRef} type="date" className="form-control" id="fechagasto"/>
                </div>
                <button type="submit" className="btn btn-primary btn-block">Crear gasto</button>
            </fieldset>
                {
                    this.state.error ?
                <div className="alert alert-danger mt-5 text-center">Todos los campos obligatorios</div> : null
                 }
        
            
            </form>
        )
    }
}
